@import './mixins';
@import './theme';

*,
::after,
::before {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

button {
  padding: 0;
  border: none;
  appearance: none;
  outline: none;
  background: none;
  cursor: pointer;
}

.hidden {
  display: none;
}

.grid {
  display: grid;
  grid-template-columns: 100%;
  grid-gap: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  align-content: center;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  z-index: -1;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $color-black;
  opacity: 0.5;
}

.linkWrapper {
  font-size: 0;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
}

.breadcrumbs {
  color: $color-white;
  font-family: 'Univers LT Roman';
  font-size: 12px;

  a {
    color: $color-white;
    font-family: 'Univers LT Roman';
    font-size: 12px;
    transition: color 0.3s ease-in-out;

    &:hover {
      color: $color-orange;
    }
  }
}
